.Square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 54px;
  line-height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 107px;
  height: 107px;
  transition: 1s;
}

.Square:focus {
  outline: none;
}

.Square:hover {
  background-color:lightblue;
}

.Active {
  background-color:limegreen;
}